@import url(https://fonts.googleapis.com/css?family=Bungee|Montserrat|Open+Sans&display=swap);
body {
  margin: 0;
  font-family: 'Bungee', 'Montserrat', 'Open Sans',  -apple-system, BlinkMacSystemFont,  "Roboto", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000; }

a {
  text-decoration: none; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

.header {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  height: 70px;
  padding: 0 25px;
  box-sizing: border-box; }

.section-profile {
  background-color: #f2f2f2; }

.main {
  text-align: center; }
  .main .title {
    padding-top: 20px;
    font-size: 170px;
    color: #fff;
    background-image: url(https://static.pexels.com/photos/4827/nature-forest-trees-fog.jpeg);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    -webkit-animation: sliding-background 20s linear infinite;
            animation: sliding-background 20s linear infinite; }

@-webkit-keyframes sliding-background {
  0%,
  100% {
    background-position: 0 40%; }
  50% {
    background-position: 20% 40%; } }

@keyframes sliding-background {
  0%,
  100% {
    background-position: 0 40%; }
  50% {
    background-position: 20% 40%; } }

.menu a {
  position: relative;
  display: inline-block;
  color: #0db393;
  margin: 0 5px;
  padding: 0 10px 5px;
  font-size: 13px;
  font-family: 'Montserrat'; }
  .menu a:hover {
    font-weight: bold; }
    .menu a:hover:after {
      right: 0; }
  .menu a:after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 100%;
    right: 100%;
    height: 2px;
    background-color: #0db393;
    -webkit-transition: right .2s linear;
    transition: right .2s linear; }

.profile {
  text-align: center; }
  .profile .keyword {
    display: inline-block;
    font-family: 'Open Sans';
    font-size: 30px; }
    .profile .keyword h2 {
      margin: 0;
      font-size: 70px;
      line-height: 1;
      font-weight: bold; }
      .profile .keyword h2:after {
        content: '';
        display: block;
        position: absolute; }
    .profile .keyword sup {
      display: block;
      text-align: left; }
    .profile .keyword .chance {
      -webkit-transform: translateY(-100%);
              transform: translateY(-100%);
      opacity: 0;
      -webkit-transition: all 1s .5s linear;
      transition: all 1s .5s linear; }
      .active .profile .keyword .chance {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0); }
  .profile .slogan p {
    opacity: 0;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    -webkit-transition: all 1s 2s linear;
    transition: all 1s 2s linear; }
    .active .profile .slogan p {
      opacity: 1;
      -webkit-transform: translateY(0);
              transform: translateY(0); }
    .profile .slogan p:first-child {
      font-size: 20px;
      -webkit-transition-delay: 1.5s;
              transition-delay: 1.5s; }

.list {
  display: flex;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 0 auto;
  padding: 50px 0; }
  .list .chip {
    flex: 0 1 50%;
    min-width: 50%;
    text-align: center;
    font-family: 'Montserrat';
    font-size: 13px; }
  .list .item {
    display: block;
    height: 100%;
    padding: 15px;
    color: #000;
    box-sizing: border-box; }
    .list .item .thumbnail {
      position: relative;
      display: block;
      padding-bottom: 56.25%;
      overflow: hidden;
      background-color: #f2f2f2; }
      .list .item .thumbnail > span {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        display: block; }
        .list .item .thumbnail > span img {
          width: 100%;
          object-fit: cover; }
      .list .item .thumbnail i {
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-image: url("/images/project/empty.png");
        background-repeat: no-repeat;
        background-position: 50% 50%;
        opacity: .5; }
    .list .item .data {
      margin-top: 15px; }
      .list .item .data li {
        margin: 3px 0;
        text-transform: capitalize; }
      .list .item .data strong {
        position: absolute;
        width: 1px;
        height: 1px;
        margin: -1px;
        padding: 0;
        border: 0;
        clip: rect(0 0 0 0);
        overflow: hidden; }
      .list .item .data .name {
        color: #0db393; }

